.popup {
  width: 100%;
  height: 100%;
  position: fixed;
  display: flex;
  top: 0;
  left: 0;
  z-index: 1;
  transition: opacity 1s ease;
}

.popup__overlay {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: #111;
  opacity: .8;
}

.popup__container {
  position: fixed;
  width: auto;
  max-height: 90vh;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(3, auto);
  grid-template-rows: repeat(2, auto);
  gap: 10px;
  position: relative;
}

.popup__side {
  width: 50px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.popup__button-block {
  display: flex;
  flex-direction: column;
}

.popup__center {
  display: flex;
  flex-direction: column;
}

.popup__image {
  max-width: 70vw;
  max-height: 70vh;
  z-index: 1;
}

.popup__text {
  grid-column-start: 2;
  color: white;
  font-size: 20px;
  text-align: start;
  padding: 0;
  margin: 0;
}

.popup__button {
  /* position: absolute;
  top: -50px;
  right: -90px; */
  width: 50px;
  height: 50px;
  z-index: 2;
}

